import axios from "axios";
import PostForm from "@/components/admin/PostForm";
export default {
  components: {
    PostForm,
  },
  methods: {
    savePost(post) {
      this.$store.dispatch("addPost", post).then((response) => {
        this.$router.push("/admin");
      });
    },
  },
};
